	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Ich bin ein Slacker","date":"11 Aug 2002","slug":"/2002-08-11-ich-bin-ein-slacker/","tags":"Y2002"},"html":"<p>Weekly Standard (via <a href=\"http://www.instapundit.com/archives/week_2002_08_11.php#002940\">InstaPundit</a>): <a href=\"http://www.weeklystandard.com/Content/Public/Articles/000/000/001/543yatms.asp\">Ich Bin Ein Slacker</a>. Right facts, wrong conclusion. The reason service is so bad in restaurants (and shops) in Germany is not the 'demise of the German work ethic'. As far as I know, German service has <i>never</i> been good. Restaurants and shops are chronically short staffed, and customers are seen by employees as an annoyance. But this is hardly news.</p>\n<p>And, at least in Niedersachsen, we <i>can</i> rent videos on Sundays.</p>"},"next":{"fields":{"title":"You Can Dance For Inspiration","date":"10 Aug 2002","slug":"/2002-08-10-you-can-dance-for-inspiration/"}},"previous":{"fields":{"title":"A Dynamic Archive Template for MT: First Draft","date":"12 Aug 2002","slug":"/2002-08-12-a-dynamic-archive-template-for-mt-first-draft/"}}},{"node":{"fields":{"title":"You Can Dance For Inspiration","date":"10 Aug 2002","slug":"/2002-08-10-you-can-dance-for-inspiration/","tags":"Y2002"},"html":"<p>PapaScott has been lately suffering from an overabundance of work (we found out the hard way that a <strike>universal</strike>uninterruptable power supply can be a single point of failure... I leave details to the imagination) and a shortage of inspiration. The situation will only become worse in the next couple of weeks, as Christopher's grandparents are coming for an extended visit, and Mama and I are dividing the hosting responsibilities (subtitle for this post: 'Separate Vacations'). I'm not sure whether the effect on the weblog will be noticeable (can a decrease in creativity be noticed when the amount of creativity was quite small to begin with?), but at least I'll be feeling less bad about it. Or at least more justified. Or whatever.</p>"},"next":{"fields":{"title":"PHP. MySQL. Oh. Es. Ex.","date":"10 Aug 2002","slug":"/2002-08-10-php-mysql-oh-es-ex/"}},"previous":{"fields":{"title":"Ich bin ein Slacker","date":"11 Aug 2002","slug":"/2002-08-11-ich-bin-ein-slacker/"}}},{"node":{"fields":{"title":"PHP. MySQL. Oh. Es. Ex.","date":"10 Aug 2002","slug":"/2002-08-10-php-mysql-oh-es-ex/","tags":"Y2002"},"html":"<p>View from an Iowa Homestead: <a href=\"http://iowa.weblogger.com/2002/08/09\">Setting up PHP and MySQL on OS X</a>. Seems awfully difficult to me, compared with 'portinstall mod_php4 mysql-server\" on FreeBSD (or apt-get under Debian), but of course getting to the point where you can type 'portinstall whatever' is the tricky part. (And I won't even mention rpm-based Linux distributions.)</p>"},"next":{"fields":{"title":"Adventures With Windows CE","date":"10 Aug 2002","slug":"/2002-08-10-adventures-with-windows-ce/"}},"previous":{"fields":{"title":"You Can Dance For Inspiration","date":"10 Aug 2002","slug":"/2002-08-10-you-can-dance-for-inspiration/"}}}],"pathPrefix":"","first":false,"last":false,"index":860,"pageCount":1101}}

